@use "variables" as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$fuentePrincipal;
  font-size: 1.6rem;
  background-color: darken(v.$grisClaro, 3%);
}
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
img {
  max-width: 100%;
  display: block;
}
ul {
  padding: 0;
  list-style: none;
  margin: 0;
}
